import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { URLSearchParams } from '@angular/http';

import { JsonpService } from '../jsonp.service';

@Component({
  selector: 'my-app',
  templateUrl: './top.component.html',
  styleUrls: ['./top.component.css']
})
export class TopComponent implements OnInit {

  constructor(private route: ActivatedRoute, private jsonpService: JsonpService) { }

  incidentList = [];

  ngOnInit() {
    this.route.data.subscribe(obj => console.log(obj['category']));

    // 
    this.jsonpService.requestGet('IncidentListDataGet.php', new URLSearchParams())
    .subscribe(
    data => {
      // 
      if (data[0]) {
        let list = data[0];
        if (list.result !== '' && list.result == true) {
          // 
          this.setDspParam(data.slice(1)); // 1
        }
      }
    },
    error => {
      // 
      console.log(error);
      console.log('');
      return false;
    }
    );
  } 

  // 
  showDetail() {
    
  }

  // 
  setDspParam(data) {
    this.incidentList = data;
  }

  // 
  onKeyWordEnter(value: string) {
    console.log("");
    console.log(value);
    value= this.encodeUnicode(value);
    window.location.href='http://localhost:4200/#/list/k/'+value;
  }

  // 16
  encodeUnicode(str) {
    var res = [];  
    for ( var i=0; i<str.length; i++ ) {
      res[i] = ( "00" + str.charCodeAt(i).toString(16) ).slice(-4);
    }
    return res.join("\\u");
  }
}


