import { Component } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { URLSearchParams } from '@angular/http';

import { JsonpService } from '../jsonp.service';

@Component({
  selector: 'my-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent {

    constructor(private route: ActivatedRoute, private jsonpService: JsonpService) {}
    ngOnInit() {
      this.searchConditionName();
    }

    condList = [];

    // 
    searchConditionName() {

      // 
      let ps = new URLSearchParams();

      // 
      this.jsonpService.requestGet('IncidentListConditionDelete.php', ps)
      .subscribe(
      data => {
        // 
        console.log(data);
        if (data[0]) {
          let list = data[0];
          if (list.result !== '' && list.result == true) {
            // 
            this.setDspParam(data.slice(1)); // 1
          }
        }
      },
      error => {
        // 
        console.log(error);
        console.log('');
        return false;
      }
      );
    }

    // 
    setDspParam(data) {
      this.condList = data;
    }

    // 
    onKeyWordEnter(value: string) {
      console.log("");
      console.log(value);
    }

    // 
    logout() {
      console.log("");
    }

    // 
    reload() {
      window.location.reload();
    }

}


