  // 16
  transCodeToStr(data) {
    if (data == '') return '';
    data = data.split('\\u');
    var str ='';
    for (var i = 0; i < data.length; i++) {
      str+=String.fromCharCode(parseInt(data[i],16));
    }
    return str;
  }

