  // 
  getDateStringFromDate(date) {

    if (date && date.getFullYear()) {
      var y:number = date.getFullYear();
      var m:number = date.getMonth();
      m++;
      var d:number = date.getDate();
      // 2018.01.20 Newtouch start
      // return  y + "-" + m + "-" + d + " 00:00:00";
      return  y + "-" + m + "-" + d;
      // 2018.01.20 Newtouch end
    } else {
      // 
      return null;
    }

  }

