import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { URLSearchParams } from '@angular/http';

import { JsonpService } from '../jsonp.service';
import { WindowRefService } from '../windowRef.service';

@Component({
  selector: 'my-mr2',
  templateUrl: './mr2.component.html',
  styleUrls: ['./mr2.component.css']
})
export class Mr2Component implements OnInit {

  nativeWindow: any;
  constructor(private route: ActivatedRoute, private jsonpService: JsonpService, private winRef: WindowRefService) {
    this.nativeWindow = winRef.getNativeWindow();
  }

  mr2Data = [];

  ngOnInit() {

    this.route.data.subscribe(obj => console.log(obj['category']));

    let ps = new URLSearchParams();
    let mkbId = this.route.snapshot.paramMap.get('mkbId');
    if (mkbId) {
      ps.set('callNo', mkbId); // TODO API TEST
      ps.set('mkbId', mkbId);
    }

    // 
    this.jsonpService.pscApiRequestGet('mr2HeadData', ps)
      .subscribe(
      data => {
        // 
        console.log("");
        console.log(data);
        // if (data[0]) {
        //   let list = data[0];
        //   if (list[0].result !== '' && list[0].result == true) {
        //     // 
        //     this.setDspParam(list.slice(1)); // 1
        //   }
        // }
      },
      error => {
        // 
        console.log("");
        console.log(error);
        console.log('');
        return false;
      }
      );

    // 
    this.jsonpService.pscApiRequestGet('mr2HeadData', ps)
      .subscribe(
      data => {
        // 
        console.log("MKBID");
        console.log(data);
        // if (data[0]) {
        //   let list = data[0];
        //   if (list[0].result !== '' && list[0].result == true) {
        //     // 
        //     this.setDspParam(list.slice(1)); // 1
        //   }
        // }
      },
      error => {
        // 
        console.log("MKBID");
        console.log(error);
        console.log('');
        return false;
      }
      );
  }


  // 
  setDspParam(data) {
    this.mr2Data = data;
  }

}


